import React from "react";
import styled from "styled-components";
import BlogCard from "../../common/components/BlogCard";

const BlogDetail = ({ className }) => {
  return (
    <div className={className}>
      <p className="fs__smallest text-center fw-bold">Đã đăng 22/3/2021</p>
      <p className="fs__title--main fw-bolder text-center w-50 mx-auto mb-5">
        Advancing our efforts to make Twitter a safer place for advertisers
      </p>
      <div className="justify-content-center">
        <img
          src="https://www.w3schools.com/css/img_mountains.jpg"
          alt=""
          className="blog__detail__img w-100"
        />
      </div>
      <main className="container blog__detail__content">
        <div className="col-8 justify-content-center mt-5 mx-auto">
          <p className="fs__text">
            Lorem, ipsum dolor sit amet consectetur adipisicing elit. Unde quae minus alias possimus
            eius blanditiis doloremque distinctio, temporibus ad. Quod, quos voluptas natus quam
            repudiandae illum nisi ea et delectus? Illum voluptas est ab aut, debitis, illo libero
            temporibus facilis omnis non quis corporis harum iure quo? Voluptate quam, incidunt
            nulla commodi quae ipsa at et, nemo, illo molestiae voluptates! Lorem ipsum dolor, sit
            amet consectetur adipisicing elit. Perferendis at iste, expedita, debitis architecto
            ducimus temporibus quae, consequuntur cum nihil officiis fugit ad illum ut harum totam
            magni adipisci labore.
          </p>
          <p className="fs__text">
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Quos laborum qui quod vitae
            officia iusto explicabo facilis rerum quo cumque. Ducimus soluta ipsum doloribus esse.
            Dolorem exercitationem nulla cupiditate laboriosam.
          </p>
          <div className="row my-5">
            <div className="col-6 blog__detail__img__content">
              <img src="https://www.w3schools.com/css/img_mountains.jpg" alt="Anh Blog" />
            </div>
            <div className="col-6 blog__detail__img__content">
              <img src="https://www.w3schools.com/css/img_mountains.jpg" alt="Anh Blog" />
            </div>
          </div>
          <p className="fs__text">
            Lorem ipsum dolor, sit amet consectetur adipisicing elit. Maiores ducimus asperiores
            neque consectetur incidunt, sunt eveniet repellat ut earum perferendis, nihil assumenda
            voluptas aspernatur soluta, dignissimos consequatur architecto quidem nemo. Repellendus,
            nisi perferendis! Necessitatibus, incidunt consectetur. Magnam mollitia ipsum odit,
            tenetur nesciunt quae pariatur ad, consectetur quis fugit blanditiis, animi eaque unde.
            Odio tempore optio quod autem odit, laudantium unde!
          </p>
        </div>
      </main>
      <div className="container my-5">
        <p className="fs__title--main text-center my-5">Đề xuất</p>
        <div className="row blog__detail__related">
          {[...Array(3)].map((_, idx) => (
            <div className="col-12 col-md-4 mb-4 mb-md-0" key={idx}>
              <BlogCard
                name="Lorem ipsum dolor sit amet consectetur adipisicing elit. Libero, molestiae."
                slug="dfad"
                thumbnail="https://www.w3schools.com/css/img_mountains.jpg"
              />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default styled(BlogDetail)`
  .blog__detail {
    &__img {
      // width: 800px;
      height: 400px;
      object-fit: cover;
      &__content {
        img {
          width: 100%;
          height: 200px;
          object-fit: cover;
        }
      }
    }
    &__content {
      p:first-child::first-letter {
        color: ${(props) => props.theme.colorTextTitle};
        padding: 0 0.3rem;
        margin: 15px 0.3rem 0 0;
        font-size: 5.5rem;
        font-weight: bolder;
        float: left;
        line-height: 0.5;
      }
    }
  }
`;
